<template>
    <ion-page>
        <ion-header>
            <ion-toolbar>
                <ion-buttons slot="start">
                    <ion-back-button default-href="/order-list"></ion-back-button>
                </ion-buttons>
                <ion-title>點燈完成</ion-title>
                <ion-buttons slot="end" v-show="stage == 7">
                    <ion-button router-link="/home">
                        <ion-icon slot="icon-only" :src="home"></ion-icon>
                    </ion-button>
                </ion-buttons>
            </ion-toolbar>
        </ion-header>
        <ion-content class="order-completed">
            <div class="bg" :style="flash">
                <div class="content">
                    <template v-if="order">
                        <ion-grid>
                            <ion-row class="ion-justify-content-around">
                                <ion-col class="ion-text-center" :key="item.id" size="4" v-for="item in order.items">
                                    <ion-text>{{ item.name }}</ion-text>
                                    <remote-img :path="item.pet_image && item.pet_image !== '' ? item.pet_image : item.icon" :router-link="'/completed-item/' + id +  '/' + item.id"></remote-img>
                                </ion-col>
                            </ion-row>
                        </ion-grid>
                    </template>
                </div>
            </div>
        </ion-content>
        <ion-footer>
            <ion-img class="step-bar" :src="`/assets/images/step-${stage == 7 ? '6': '5'}.png`"></ion-img>
        </ion-footer>
    </ion-page>
</template>

<script>
    import {
        queryOrder
    } from "../api";

    import {
        IonBackButton,
        IonButton,
        IonButtons,
        IonCol,
        IonContent,
        IonFooter,
        IonGrid,
        IonHeader,
        IonIcon,
        IonImg,
        IonPage,
        IonRow,
        IonText,
        IonTitle,
        IonToolbar
    } from "@ionic/vue";

    import {
        home
    } from "ionicons/icons";

    import RemoteImg from "@/views/RemoteImg.vue";

    export default {
        components: {
            IonBackButton,
            IonButton,
            IonButtons,
            IonCol,
            IonContent,
            IonFooter,
            IonGrid,
            IonHeader,
            IonIcon,
            IonImg,
            IonPage,
            IonRow,
            IonText,
            IonTitle,
            IonToolbar,
            RemoteImg
        },
        computed: {
            flash() {
                let index = 5;
                if (this.stage === 2 || this.stage === 4 || this.stage === 6) {
                    index = 6;
                } else if (this.stage === 7) {
                    index = 7;
                }
                return {
                    backgroundImage: `url(/assets/images/bg${index}.png)`,
                    transition: "background-image 0.75s ease-in-out"
                };
            }
        },
        data() {
            return {
                id: this.$route.params.id,
                order: false,
                stage: 1,
                handle: false,
                home
            };
        },
        methods: {
            change() {
                if (this.stage < 7) {
                    this.handle = setTimeout(() => {
                        this.stage += 1;
                        this.change();
                    }, 1500);
                } else {
                    this.handle = false;
                }
            },
            query() {
                queryOrder(this.id, (result) => {
                    this.order = result.order;
                    this.change();
                });
            }
        },
        mounted() {
            this.query();
        },
        watch: {
            $route(to) {
                if (to.name === "OrderCompleted") {
                    this.id = to.params.id;
                    this.stage = 1;
                    this.query();
                } else {
                    if (this.handle) {
                        clearTimeout(this.handle);
                        this.handle = false;
                    }
                }
            }
        }
    };
</script>

<style scoped>
    ion-text {
        color: red;
    }
    .bg {
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        width: 100%;
    }
    .content {
        bottom: 50px;
        position: absolute;
    }
    .step-bar {
        background: #ece1a6;
    }
</style>
